<template>
    <div>
        <br>
        <form @submit="addTodo">
            <input type="text" v-model="title" name="title" placeholder="Add Todo ...">
            <input type="submit" value="Save" class="btn">
        </form>
        <br>
    </div>
</template>

<script>
import uuid from 'uuid';
export default {
    name: "AddTodo",
    data() {
        return {
            title: ''    
        }
    },
    methods: {
        addTodo(e) {
            e.preventDefault();
            const newToDo = {
                //id: uuid.v4(),
                title: this.title,
                completed: false
            }
            // Send up to parent
            this.$emit('add-todo', newToDo);
            this.title = '';

        }
    }
}
</script>

<style scoped>
form {
    display: flex;
}

input[type="text"] {
    flex:10;
    padding: 5px;
}
input[type="submit"] {
    flex: 2;
} 
</style>


